let grid = document.querySelector('.grid')

let scoreDisplay = document.querySelector('#score')
const blockWidth=100
const blockHeight=20
const boardWidth = 560
const boardHeight = 300

const startPosition = [230,10]
const ballDiamater = 20
let currentPosition = startPosition
let timerId 
const ballStart = [270,40]
let ballCurrentPosition = ballStart
let xDirection = 2
let yDirection = 2
let score=0

class Block{
    constructor(xAxis,Yaxis){
        this.bottomLeft = [xAxis,Yaxis]
        this.bottomRight = [xAxis + blockWidth,Yaxis]
        this.topLeft = [xAxis,Yaxis+blockHeight]
        this.topRight = [xAxis + blockWidth,Yaxis+blockHeight]
    }
}

const blocks = [
    new Block(10,270),
    new Block(120,270),
    new Block(230,270),
    new Block(340,270),
    new Block(450,270),
    new Block(10,240),
    new Block(120,240),
    new Block(230,240),
    new Block(340,240),
    new Block(450,240),
    new Block(10,210),
    new Block(120,210),
    new Block(230,210),
    new Block(340,210),
    new Block(450,210),
]


function addBlocks (){
    
   for(let i=0;i<blocks.length;i++){
        const block = document.createElement('div')
        block.classList.add('block')
        block.style.left = blocks[i].bottomLeft[0] + 'px'
        block.style.bottom = blocks[i].bottomLeft[1] + 'px'
        grid.appendChild(block)
        
   }
}


    
function drawUser(){
    user.style.left = currentPosition[0]+'px'
    user.style.bottom = currentPosition[1]+'px'    
}

function drawBall(){
    ball.style.left = ballCurrentPosition[0]+'px'
    ball.style.bottom = ballCurrentPosition[1]+'px'
}

function moveBall(){
    ballCurrentPosition[0] += xDirection
    ballCurrentPosition[1] += yDirection
    drawBall()
    checkCollisions()
}

function checkCollisions(){
    //Blocks Collisions
    for(let i=0;i<blocks.length;i++){
        if(
           (ballCurrentPosition[0] > blocks[i].bottomLeft[0] && ballCurrentPosition[0] < blocks[i].bottomRight[0])
           &&
           ((ballCurrentPosition[1] + ballDiamater)>blocks[i].bottomLeft[1] && ballCurrentPosition[1] < blocks[i].topLeft[1]) 
        ){
            let allBlocks = Array.from(document.querySelectorAll('.block'))
            allBlocks[i].classList.remove('block')
            blocks.splice(i,1)
            changeDirection()
            score++;
            scoreDisplay.innerHTML=score
            
            //Check Win
            if(blocks.length === 0 ){
                scoreDisplay.innerHTML = "YOU WIN"
                clearInterval(timerId)
                document.removeEventListener('keydown',moveUser)
            }
        }
    }
    //User Collision
    if(
        (ballCurrentPosition[0] > currentPosition[0] && ballCurrentPosition[0] < currentPosition[0] + blockWidth)
        &&
        (ballCurrentPosition[1] > currentPosition[1] && ballCurrentPosition[1] < currentPosition[1] + blockHeight)
       ){
         changeDirection()
       }
    

    //Board Collision
    if(
        ballCurrentPosition[0]>=(boardWidth - ballDiamater) ||
        ballCurrentPosition[1]>=(boardHeight - ballDiamater) ||
        ballCurrentPosition[0]<=0    
      ){
        changeDirection()
    }
    if(ballCurrentPosition[1]<=0){
        clearInterval(timerId)
        scoreDisplay.innerHTML="you Lose"
        document.removeEventListener('keydown',moveUser)
    }
}

function changeDirection(){
    if(xDirection === 2 && yDirection === 2){
        yDirection = -2
        return
    }
    if(xDirection === 2 && yDirection == -2){
        xDirection = -2
        return
    }
    if(xDirection === -2 && yDirection == -2){
        yDirection = 2
        return
    }
    if(xDirection === -2 && yDirection == 2){
        xDirection = 2
        return
    }
}


function moveUser(e){
    switch(e.key){
        case 'ArrowLeft':
            if(currentPosition[0]>0){
                currentPosition[0] -= 10 
                drawUser()
            }
            break;
        case 'ArrowRight':
            if(currentPosition[0]< boardWidth - 100){
                currentPosition[0] += 10 
                drawUser()
            }
            break;
        }
}
const user = document.createElement('div')
const ball = document.createElement('div')

export function mainBreakOut(){
    grid = document.querySelector(".grid")
    scoreDisplay = document.querySelector('#score')
    user.classList.add('user')
    ball.classList.add('ball')
    drawBall()
    drawUser()
    addBlocks()
    grid.appendChild(user)
    grid.appendChild(ball)    
    timerId=setInterval(moveBall,30)  
    document.addEventListener('keydown',moveUser)
    const t = document.querySelector('.restarting')
    t.addEventListener('click',()=>{
        console.log("restart")
        grid.innerHTML=""
        scoreDisplay.innerHTML = ""
        clearInterval(timerId)
        mainBreakOut()
    })
}

function restart(){
    const t = document.querySelector('.restarting')
    t.addEventListener('click',()=>{
        console.log("restart")
        grid.innerHTML=""
        scoreDisplay.innerHTML = ""
        clearInterval(timerId)
        mainBreakOut()
    })
}
